.services__h2 {
	color: $h2-color-mod;
	letter-spacing: -3px;
	margin-bottom: 84px;
	@include r(768) {
		margin-bottom: 30px;
		letter-spacing: 0; } }
.services {
	padding-top: 78px;
	padding-bottom: 70px;
	@include r(768) {
		padding-top: 30px;
		padding-bottom: 35px; } }
.work-list {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	@include r(768) {
		justify-content: center; } }
.work-list__item {
	text-align: center;
	color: $main-text-color;
	font: $main-text-style;
	flex-basis: 263px;
	flex-grow: 0;
	flex-shrink: 1;
	margin-bottom: 62px;
	@include r(768) {
		font: $main-text-style-mobile;
		margin: 0 5px 40px; }
	span {
		display: block;
		color: #292f36;
		font: 700 17px "Quattrocento Sans";
		text-transform: uppercase;
		margin-top: 26px;
		margin-bottom: 20px;
		word-spacing: -2px; }
	.icon {
		height: 3em;
		width: 3em; } }
